*,
*::before, 
*::after{
    margin: 0;
    padding: 0;
    box-sizing: inherit;
}

html{
    font-size: 62.5%;
}

body{
    text-align: center;
    padding: 1rem 0rem;
    background-color: #111;
    color: white;
    font-family: Arial, Helvetica, sans-serif;
    
}

.mem-btn{
    cursor: pointer;
    font-style: italic;
    padding: 10px;
}

.form{
    display:flex;
    justify-content: center;
}
.leftmargin{
    margin-left: 1rem;
}

.inputstyle{
    border: none;
    border-radius: 4px;
    color: gray;
    padding: 0.5rem 1rem;
}

.top-margin{
    margin-top: 2rem;
}

.footer{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 90%;
    border-top: 1px solid rgba(255,255,255, .1);
   
}   

.footer_style{
    width: 100%;
    height: 100px;
}

.footer_Item{
    display: flex;
    justify-content: space-evenly;
    opacity: .5;
    align-items: center;
}
.footer_list{
    list-style: none;
    float: left;
    margin: 1rem;
    text-align: center;
}

.mem-iteam{
    text-align: left;
    display: inline-block;
    padding: 1rem;
    position: relative;
    width: 150px;
    height: 150px;
    margin: 5px;
    cursor: pointer;
}

.mem-img{
    width: 150px;
    height: 150px;
    border-radius: 8px;
    border: 3px solid white;
    position: absolute;
    object-fit: cover;
    filter: brightness(90%);
}

.my-mem-img{
    position: relative;
    display: inline-block;
    margin: 5px;
    width: 220px;
    border-radius: 4px;
    border: 3px solid white;
}


.mem-text{
    width: 150px;
    position: absolute;
    text-align: center;
    border-radius: 5px;
    bottom: 0;
    animation-name: fadein;
    animation-duration: 1s;
    font-size: 14px;
}

.no-txt{
    display: none;
}

.textclr{
    color: white;
}

.dark-img{
    background-color: gray;
    filter: brightness(10%);
    animation-name: darken;
    animation-duration: 1s;
}

@keyframes fadein{
    from {opacity: 0;}
    to{opacity: 1;}
}

@keyframes darken{
    from{filter:brightness(90%)}
    to{filter: brightness(10%);}
}